<template>
  <!--  -->
  <div class="firstPage">
    <home>
      <main class="main">
        <el-row type="flex" justify="center">
          <el-col :span="18" class="main_content">
            <el-card>
              <el-row type="flex" justify="center">
                <el-col :span="18">
                  <div class="dream">
                    <div class="title center-align">
                      <i class="el-icon-s-opportunity"></i>&nbsp;&nbsp;
                    </div>

                    <div class="text">
                      
                    </div>
                  </div>

                  <div class="video">
                    <div class="title center-align">
                      <i class="el-icon-video-camera-solid"></i>&nbsp;&nbsp;
                      
                    </div>
                    <div id="dplayer"></div>
                  </div>

                  <div class="article">
                    <div class="title center-align">
                      <i class="el-icon-date"></i>&nbsp;&nbsp; 
                    </div>
                    <div class="full">
                      <img
                        class="full"
                        :src="staticUrl+'/static/img/artcle/jia.jpg'"
                        alt=""
                        srcset=""
                      />
                      <p class="text_mi">
                        ,:,;,?,...
                      </p>
                    </div>
                  </div>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
        </el-row>
      </main>
    </home>
  </div>
</template>
<script>
import DPlayer from "dplayer";
import home from "../home/home.vue";

export default {
  name: "firstpage",
  components: { home },
  data() {
    return {
      staticUrl:this.$staticUrl
    };
  },
  created() {
    this.$nextTick(() => {
      this.video(); // 
    });
  },
  methods: {
    // 
    video() {
      const dp = new DPlayer({
        container: document.getElementById("dplayer"),
        autoplay: false,
        hotkey: true,
        preload: "auto",
        video: {
          url: "https://blinkfox.github.io/medias/videos/demo.mp4",
          pic: "https://t1.picb.cc/uploads/2021/04/04/ZBlUGj.jpg",
        },
      });
      // dp.seek(1);
    },
  },
};
</script>
<style lang="less" scoped>
// 
.main {
  min-height: 772px;
  margin: 10px;
  .title {
    margin-bottom: 20px;
    font-size: 2rem;
    font-weight: 700;
  }
  .main_content {
    .dream {
      margin-top: 20px;
      margin-bottom: 40px;
      background: white;
    }
  }
}
</style>


